
.loader {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top: 4px solid #2563eb; 
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.alert {
  padding: 10px;
  background-color: #f44336; 
  color: white;
  border-radius: 4px;
  margin: 16px;
}

.alert-danger {
  background-color: #dc3545; 
}
.dashboard {
  padding: 24px; 
  max-width:1200px;
  margin:0 auto ; 
}
.dasboard-container{
  margin-left:170px;
  margin-top:70px;
}

.metrics-grid {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.metric-card {
  /* background-color: #f3f4f6; */
  /* border:1px solid #E0E0E0; */
  /* background-color: rgb(240 249 255) ; */
  /* background-color:rgb(250 245 255); */
  background-color: #c4eae1;
  cursor: pointer;

  width: 200px;
  height: 100px;
  border-radius:8px ;
  overflow: hidden;
  padding: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /* transition: transform 0.2s ease; */
}

/* .metric-card:hover {
  transform: translateY(-2px);
  
} */

.metric-card h3 {
  margin-bottom: 10px;
  color: #555;
}

.metric-value {
  color: #777;
  margin: 0;
  text-align: center;
}

.charts-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly; 
  gap: 30px; 
}
.charts-grid h3{
  color: #555;
}
.chart-card {
  border:1px solid #E0E0E0;
  border-radius: 8px;
  overflow: hidden;
  margin-top: 30px;
  flex: 1 1 400px; 
  min-width: 400px; 
}
.raw-material-finished-goods {
  display: flex;
  justify-content: space-between; 
  gap:30px;
  margin-top: 20px; 
}

.raw-material, .finished-goods {
  flex: 1; 
  padding: 10px; 
  border-radius: 5px; 
  text-align: center; 
}
.metric-values, .card-subtitle{
  margin-left: 18px;
  margin-bottom: 20px;
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 18px;
  color: #666;
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #dc2626;
  font-size: 18px;
}

.production-circle-card {
  min-height: 400px;
  width:500px;
  border:1px solid #E0E0E0;
  margin-top: 30px;
  border-radius: 8px;
}

.production-progress {
  padding: 1.5rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.circular-progressbars {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
  padding: 1rem;
}

.progress-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Apply staggered animation delays using nth-child */
.circular-progressbars .progress-item:nth-child(1) {
  animation-delay: 0.3s;
}

.circular-progressbars .progress-item:nth-child(2) {
  animation-delay: 0.6s;
}

.circular-progressbars .progress-item:nth-child(3) {
  animation-delay: 0.9s;
}

.progress-circle {
  width: 150px;
  height: 150px;
}

.progress-label {
  font-size: 1rem;
  font-weight: 500;
  color: #4b5563;
  text-align: left;
}
.production-value{
  text-align: center;
}

.growth-rate {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: #f3f4f6;
  border-radius: 0.5rem;
  margin-top: 1rem;
}

.growth-label {
  font-size: 0.875rem;
  color: #6b7280;
}

.growth-value {
  font-size: 1rem;
  font-weight: 600;
}

.growth-value.positive {
  color: #16a34a;
}

.growth-value.negative {
  color: #dc2626;
}

.metrics-details {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding: 1rem;
}

.metric-detail-card {
  /* border:1px solid #E0E0E0; */
  /* background-color: #f3f4f6; */
  background-color: #c4eae1;
  padding: 16px;
  border-radius: 8px;
  text-align: center;
  overflow:hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.metric-detail-card h4 {
  color: #555;
  margin-bottom: 0.5rem;
}

.metric-detail-card .metric-value {
  color: #777;
  font-weight: normal;
}

@media (max-width: 768px) {
  .metrics-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .charts-grid {
    flex-direction: column; /* Stack charts on small screens */
    align-items: center;
  }

  .chart-card {
    overflow-x: auto;
  }
   .chart-card {
    overflow-x: auto;
  }

  .circular-progressbars {
    flex-direction: column; /* Stack progress bars on small screens */
    align-items: flex-start; /* Align items to the start */
  }

  .progress-item {
    flex-direction: row; /* Keep the direction for better readability */
    width: 100%;
  }

  .progress-label {
    text-align: left;
  }
}
